@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");
// 'Montserrat', sans-serif;
@font-face {
  font-family: "Calibri";
  src: url("../../fonts/calibri.ttf") format("truetype");
}

@font-face {
  font-family: "Calonna";
  src: url("../../fonts/ColonnaMT.ttf") format("truetype");
}

:root {
  --font-primary: "Montserrat", sans-serif;
  --font-secondary: serif;

  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;

  /* line-height */
  --heading-line-height: 1.2;
  --body-line-height: 1.5;

  /* set base values */
  --text-base-size: 1em;
  --text-scale-ratio: 1.2;

  /* type scale */
  --text-xs: calc(
    1em / (var(--text-scale-ratio) * var(--text-scale-ratio))
  ); /*11.11px*/
  --text-sm: calc(1em / var(--text-scale-ratio)); /*13.33*/
  --text-md: calc(1em * var(--text-scale-ratio)); /*19.2*/
  --text-lg: calc(
    1em * var(--text-scale-ratio) * var(--text-scale-ratio)
  ); /*23.04*/
  --text-xl: calc(
    1em * var(--text-scale-ratio) * var(--text-scale-ratio) *
      var(--text-scale-ratio)
  ); /*27.648*/
  --text-xxl: calc(
    1em * var(--text-scale-ratio) * var(--text-scale-ratio) *
      var(--text-scale-ratio) * var(--text-scale-ratio)
  ); /* 33.17 */
  --text-xxxl: calc(
    1em * var(--text-scale-ratio) * var(--text-scale-ratio) *
      var(--text-scale-ratio) * var(--text-scale-ratio) *
      var(--text-scale-ratio)
  ); /*39.81*/
}

body {
  font-size: var(--text-base-size);
  font-family: var(--font-primary);
  color: var(--color-text);
}

h1,
h2,
h3,
h4,
form legend {
  // color: var(--color-text-heading);
  color: var(--color-text);
  line-height: var(--heading-line-height);
}

a {
  color: var(--color-link);

  &:visited {
    color: var(--color-link-visited);
  }
}

/* text size */
.text--xxxl {
  font-size: var(--text-xxxl);
}

h1,
.text--xxl {
  font-size: var(--text-xxl);
}

h2,
.text--xl {
  font-size: var(--text-xl);
}

h3,
.text--lg {
  font-size: var(--text-lg);
}

h4,
.text--md {
  font-size: var(--text-md);
}

.text--sm,
small {
  font-size: var(--text-sm);
}

.text--xs {
  font-size: var(--text-xs);
}

p {
  line-height: var(--body-line-height);
  color: var(--color-text);
}

a {
  color: var(--color-link);

  &:visited {
    color: var(--color-link-visited);
  }
}

b,
strong {
  font-weight: bold;
}

// spacing will be handled separately

.text-container {
  //   h2,
  //   h3,
  //   h4 {
  //     margin-top: var(--space-sm);
  //   }

  //   ul,
  //   ol,
  //   p {
  //     margin-bottom: var(--space-md);
  //   }

  ul,
  ol {
    list-style-position: outside;
    // padding-left: 24px;
  }

  ul {
    list-style-type: disc;
  }

  ol {
    list-style-type: decimal;
  }

  ul li,
  ol li {
    line-height: var(--body-line-height);
  }

  em {
    font-style: italic;
  }

  u {
    text-decoration: underline;
  }
}

/* utility classes */
.truncate {
  // truncate text if it exceeds parent
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-center {
  text-align: center;
}

.fw-regular {
  font-weight: var(--fw-400);
}

.fw-medium {
  font-weight: var(--fw-500);
}

.fw-semi-bold {
  font-weight: var(--fw-600);
}

.fw-bold {
  font-weight: var(--fw-700);
}

.fw-300 {
  font-weight: 300;
}

.fw-400 {
  font-weight: 400;
}

.fw-500 {
  font-weight: 500;
}

.fw-600 {
  font-weight: 600;
}

.fw-700 {
  font-weight: 700;
}

.ff-roman {
  font-family: "Times New Roman", Times, serif;
}

