/*



Update body fonsize text-sizes ==> the whole typography system
    ----> change text-base-size at a spefied mediaquery
    ----> By changing the text-base size: typography and spacing are all effected

Update text-sizes but not affect body text size
    ----> change text-scale-ratio at specific mediaquery
    ----> By changing text-scale-ratio only typography is affected which then affect spacing

Update all sizes at a component level
    ---> componet { font-size: new-value }

Update specific element inside component 
    ---> component element { font-size: var(--text-xl) }
    ---> --text-xl: is the value here    
*/
